<template>
  <section class="template-container">
    <!-- 左边栏 -->
    <section class="template-left">
      <section class="left-header">
        <span @click="handleReturn()">返回</span>
        <button @click="handleExportPDF()">导出PDF</button>
      </section>
      <section class="left-body" id="resumePdf">
        <section class="body-side">
          <section class="side-header">
            <el-upload
              class="avatar-uploader"
              action="https://run.mocky.io/v3/9d059bf9-4660-45f2-925d-ce80ad6c4d15"
              :show-file-list="false"
              :on-success="handleAvatarSuccess"
              :before-upload="beforeAvatarUpload"
            >
              <img v-if="imageUrl" :src="imageUrl" class="avatar" />
              <el-icon v-else class="avatar-uploader-icon"><Plus /></el-icon>
            </el-upload>
            <!-- <img src="../assets/images/header.jpg" style="object-fit: cover" /> -->
          </section>
          <section class="side-divide"></section>
          <section class="side-content">
            <section class="side-content-basic">
              <section class="basic-title">基本信息 Basic</section>
              <section class="basic-item">
                院校: {{ resumeInfo.base.school || "" }}
              </section>
              <section class="basic-item">
                专业: {{ resumeInfo.base.major || "" }}
              </section>
              <section class="basic-item">
                学历: {{ resumeInfo.base.degree || "" }}
              </section>
              <section class="basic-item">
                学年:
                {{ resumeInfo.base.onSchoolTime.beginTime || "" }}-{{
                  resumeInfo.base.onSchoolTime.endTime || ""
                }}
              </section>
              <section class="basic-item">
                籍贯: {{ resumeInfo.base.hometown || "" }}
              </section>
            </section>
            <section class="side-content-contact">
              <section class="contact-title">联系方式 Contact</section>
              <section class="contact-item">
                电话: {{ resumeInfo.contact.phone || "" }}
              </section>
              <section class="contact-item">
                邮箱: {{ resumeInfo.contact.email || "" }}
              </section>
            </section>
            <section class="side-content-contact">
              <section class="contact-title">求职意向 Work</section>
              <section class="contact-item">
                职位: {{ resumeInfo.work.job || "" }}
              </section>
              <section class="contact-item">
                城市: {{ resumeInfo.work.city || "" }}
              </section>
            </section>
            <section class="side-content-contact">
              <section class="contact-title">荣誉奖励 certificate</section>
              <section v-if="resumeInfo.certificateList != null">
                <section
                  class="contact-item"
                  v-for="(item, index) in resumeInfo.certificateList"
                  :key="index"
                >
                  {{ item }}
                </section>
              </section>
            </section>
          </section>
        </section>
        <section class="body-content">
          <section class="content-name">
            {{ resumeInfo.base.username || "" }}
          </section>
          <section class="content-job">{{ resumeInfo.work.job || "" }}</section>
          <section class="content-evaluation">
            {{ resumeInfo.evaluation || "" }}
          </section>
          <section class="content-skill">
            <section class="skill-title">技能证书 Skill</section>
            <el-divider />
            <ul v-if="resumeInfo.skillList != null">
              <li v-for="(item, index) in resumeInfo.skillList" :key="index">
                {{ item }}
              </li>
            </ul>
          </section>
          <section class="content-project">
            <section class="project-title">项目经历 Project</section>
            <el-divider />
            <section
              class="project-item"
              v-for="(item, index) in resumeInfo.projectExperience"
              :key="index"
            >
              <section class="project-item-title">
                <section>
                  {{ item.beginTime || "" + " - " + item.endTime || "" }}
                </section>
                <section>
                  {{ item.projectName || "" }}-{{ item.post || "" }}
                </section>
              </section>
              <ul v-if="item.parseContent != null">
                <li v-for="(text, index) in item.parseContent" :key="index">
                  {{ text }}
                </li>
              </ul>
            </section>
          </section>
          <section class="work-experience">
            <section class="experience-title">工作经历 Work</section>
            <section v-if="resumeInfo.workExperience != null">
              <section
                v-for="(item, index) in resumeInfo.workExperience"
                :key="index"
                class="experience-body"
              >
                <section
                  class="experience-item"
                  style="color: #1a406c; font-weight: bold"
                >
                  {{ compileTime(item.startTime) }}&nbsp;-&nbsp;{{
                    compileTime(item.endTime)
                  }}
                </section>
                <section
                  class="experience-item"
                  style="color: #1a406c; font-weight: bold"
                >
                  {{ item.department }}
                </section>
                <section class="experience-item" style="margin-top: 0.5rem">
                  {{ item.post }}
                </section>
                <section
                  class="experience-item-content"
                  style="margin-top: 0.5rem"
                >
                  {{ item.content }}
                </section>
              </section>
            </section>
          </section>
        </section>
      </section>
    </section>
    <!-- 右边栏 -->
    <section class="template-right">
      <ResumeSidebar />
    </section>
  </section>
</template>
<script>
import * as moment from "moment";
import { computed, defineComponent, reactive, toRefs } from "vue";
import router from "../router/index";
import { useStore } from "vuex";
import ResumeSidebar from "../components/ResumeSidebar.vue";
import { ElMessage } from "element-plus";
import { Plus } from "@element-plus/icons-vue";
import { toPrintPdf } from "../utils/toPdf.js"
export default defineComponent({
  components: {
    ResumeSidebar,
    Plus,
  },
  setup() {
    const store = useStore();
    const state = reactive({
      imageUrl: "",
    });
    const handleReturn = () => {
      router.push("/");
    };
    const resumeInfo = computed(() => {
      return store.getters["resume/getResumeInfo"];
    });
    const compileTime = (datetime) => {
      return moment(datetime).format("YYYY.MM.DD");
    };
    const handleExportPDF = () => {
      toPrintPdf("郭明简历")
    };
    const handleAvatarSuccess = (response, uploadFile) => {
      state.imageUrl = URL.createObjectURL(uploadFile.raw);
    };

    const beforeAvatarUpload = (rawFile) => {
      if (rawFile.type !== "image/jpeg") {
        ElMessage.error("Avatar picture must be JPG format!");
        return false;
      } else if (rawFile.size / 1024 / 1024 > 2) {
        ElMessage.error("Avatar picture size can not exceed 2MB!");
        return false;
      }
      return true;
    };
    return {
      resumeInfo,
      ...toRefs(state),
      handleReturn,
      compileTime,
      handleExportPDF,
      handleAvatarSuccess,
      beforeAvatarUpload,
    };
  },
});
</script>
<style lang="scss" scoped>
.template-container {
  width: 100%;
  height: 100%;
  display: grid;
  grid-template-columns: 6fr 2fr;
  .template-left {
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    padding: 0.5rem 1rem;
    .left-header {
      height: 2.5rem;
      width: 100%;
      border-radius: 0.2rem;
      color: black;
      background: #fff;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
      padding: 0rem 0.5rem;
      span {
        font-size: 0.8rem;
      }
      button {
        width: 4rem;
        height: 1.5rem;
        border-radius: 0.5rem;
        background: var(--grg_main_theme);
        color: #fff;
        font-size: 0.7rem;
      }
    }
    .left-body {
      width: 100%;
      min-height: 25rem;
      margin-top: 1rem;
      background: #fff;
      color: #27292d;
      box-sizing: border-box;
      border-radius: 0.2rem;
      font-size: 0.7rem;
      display: grid;
      grid-template-columns: 2fr 5fr;
      .body-side {
        width: 100%;
        height: 100%;
        display: flex;
        flex-flow: column;
        .side-header {
          width: 100%;
          height: 8rem;
          background: #efefef;
          display: flex;
          flex-direction: column;
          align-items: center;
          justify-content: center;
          img {
            width: 4rem;
            height: 5rem;
          }
        }
        .side-divide {
          height: 1.5rem;
          background: #f6e14b;
        }
        .side-content {
          background: #17426d;
          flex: 1;
          color: #f2ffff;
          .side-content-basic {
            width: 100%;
            min-height: 5rem;
            display: flex;
            flex-direction: column;
            box-sizing: border-box;
            padding: 0 1rem;
            line-height: 1rem;
            margin-top: 3rem;
            .basic-title {
              height: 3rem;
              width: 100%;
              font-size: 1.2rem;
              font-weight: bold;
              display: flex;
              flex-direction: column;
              justify-content: center;
              align-items: center;
            }
            .basic-item {
              font-size: 0.7rem;
            }
          }
          .side-content-contact {
            width: 100%;
            min-height: 5rem;
            display: flex;
            flex-direction: column;
            padding: 0 1rem;
            line-height: 1rem;
            margin-top: 3rem;
            .contact-title {
              height: 3rem;
              width: 100%;
              font-size: 1.2rem;
              font-weight: bold;
              display: flex;
              flex-direction: column;
              justify-content: center;
              align-items: center;
            }
            .contact-item {
              font-size: 0.7rem;
            }
          }
        }
      }
      .body-content {
        background: #fff;
        display: flex;
        flex-flow: column;
        box-sizing: border-box;
        padding: 1rem;
        color: #1a406c;
        line-height: 1rem;
        .content-name {
          font-size: 1.8rem;
          font-weight: bold;
        }
        .content-job {
          margin-top: 1rem;
          font-size: 1.3rem;
          font-weight: bold;
        }
        .content-evaluation {
          margin-top: 0.5rem;
        }
        .content-skill {
          margin-top: 1rem;
          box-sizing: border-box;
          .skill-title {
            font-size: 1.3rem;
            font-weight: bold;
          }
          ul {
            padding: 0 1rem;
            line-height: 1rem;
            margin-top: 0.5rem;
          }
        }
        .content-project {
          margin-top: 1rem;
          .project-title {
            font-size: 1.3rem;
            font-weight: bold;
          }
          .project-item {
            .project-item-title {
              width: 100%;
              display: grid;
              grid-template-columns: 1fr 2fr;
              section {
                margin-top: 0.5rem;
                font-weight: bold;
              }
            }
            ul {
              padding: 0 0.9rem;
              line-height: 1rem;
              margin-top: 0.2rem;
            }
          }
        }
        .work-experience {
          margin-top: 1rem;

          .experience-title {
            font-size: 1.3rem;
            font-weight: bold;
          }
          .experience-body {
            margin-top: 1rem;
            width: 100%;
            display: grid;
            grid-template-columns: 1fr 1fr;
            .experience-item {
              width: 100%;
            }
            .experience-item-content {
              min-height: 1rem;
              width: 100%;
            }
          }
        }
      }
    }
  }
  .template-right {
    position: relative;
    width: 100%;
    height: 100%;
    box-sizing: border-box;
  }
}
.avatar-uploader .el-upload {
  border: 1px dashed var(--el-border-color);
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
  transition: var(--el-transition-duration-fast);
}

.avatar-uploader .el-upload:hover {
  border-color: var(--el-color-primary);
}

.el-icon.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  text-align: center;
}
.avatar-uploader .avatar {
  width: 178px;
  height: 178px;
  display: block;
}
</style>