<template>
  <section class="sidebar-container">
    <section class="sidebar-title" v-show="alreadyToFill()">已添加模块</section>
    <section
      v-for="(item, index) in fillModels"
      :key="index"
      style="padding: 0.3rem; border-radius: 0.2rem"
      @click="handleModelOpen(item)"
    >
      <section class="sidebar-item">
        <section class="item-header">
          <img src="../assets/images/icon/work.png" alt="这是工作图标" />
          <span style="margin-left: 0.5rem">{{ item.name }}</span>
        </section>
        <section class="item-body">{{ item.comment }}</section>
      </section>
    </section>
    <section class="sidebar-title" v-show="notToFill()">待添加模块</section>
    <section
      v-for="(item, index) in nFillModels"
      :key="index"
      @click="handleModelOpen(item)"
    >
      <section class="sidebar-item">
        <section class="item-header">
          <img src="../assets/images/icon/work.png" alt="这是工作图标" />
          <span style="margin-left: 0.5rem">{{ item.name }}</span>
        </section>
        <section class="item-body">{{ item.comment }}</section>
      </section>
    </section>
  </section>
  <!-- 基本信息 -->
  <ResumeDialog v-model="isShowBaseDialog" :modelTitle="'基本信息'">
    <el-form label-width="3rem" label-position="top">
      <el-form-item label="名字">
        <el-input v-model="baseValue.username"></el-input>
      </el-form-item>
      <el-form-item label="院校">
        <el-input v-model="baseValue.school"></el-input>
      </el-form-item>
      <el-form-item label="专业">
        <el-input v-model="baseValue.major"></el-input>
      </el-form-item>
      <el-form-item label="学历">
        <el-input v-model="baseValue.degree"></el-input>
      </el-form-item>
      <el-form-item label="学年">
        <el-date-picker
          value-format="YYYY.MM"
          v-model="baseValue.onSchoolTime"
          type="daterange"
          range-separator="到"
          start-placeholder="入学时间"
          end-placeholder="毕业时间"
          size="default"
        />
      </el-form-item>
      <el-form-item label="籍贯">
        <el-input v-model="baseValue.hometown"></el-input>
      </el-form-item>
      <el-form-item label="自我介绍">
        <el-input type="textarea" v-model="baseValue.evaluation"></el-input>
      </el-form-item>
    </el-form>
  </ResumeDialog>
  <!-- 联系方式 -->
  <ResumeDialog v-model="isShowContactDialog" :modelTitle="'联系方式'">
    <el-form label-width="3rem" label-position="top">
      <el-form-item label="电话">
        <el-input v-model="contactValue.phone"></el-input>
      </el-form-item>
      <el-form-item label="邮箱">
        <el-input v-model="contactValue.email"></el-input>
      </el-form-item>
    </el-form>
  </ResumeDialog>
  <!-- 求职意向 -->
  <ResumeDialog v-model="isShowWorkDialog" :modelTitle="'求职意向'">
    <el-form label-width="3rem" label-position="top">
      <el-form label-width="3rem" label-position="top">
        <el-form-item label="职位">
          <el-input v-model="workValue.job"></el-input>
        </el-form-item>
        <el-form-item label="城市">
          <el-input v-model="workValue.city"></el-input>
        </el-form-item>
      </el-form>
    </el-form>
  </ResumeDialog>
  <!-- 荣誉证书 -->
  <ResumeDialog v-model="isShowCertificateDialog" :modelTitle="'荣誉奖励'">
    <el-form label-width="3rem" label-position="top">
      <el-form-item
        v-for="(domain, index) in certificateList"
        :key="index"
        label="荣誉"
      >
        <el-input v-model="domain.value" />
        <el-button
          style="margin-top: 0.2rem"
          type="danger"
          @click.prevent="removeDomain(domain)"
          >删除</el-button
        >
      </el-form-item>
      <el-form-item>
        <el-button type="warning" @click="addDomain">新增</el-button>
      </el-form-item>
    </el-form>
  </ResumeDialog>
  <!-- 技能证书 -->
  <ResumeDialog v-model="isShowSkillsList" :modelTitle="'技能证书'">
    <el-form label-width="3rem" label-position="top">
      <el-form-item
        v-for="(domain, index) in skillList"
        :key="index"
        label="技能"
      >
        <el-input v-model="domain.value" />
        <el-button
          style="margin-top: 0.2rem"
          type="danger"
          @click.prevent="removeSkill(domain)"
          >删除</el-button
        >
      </el-form-item>
      <el-form-item>
        <el-button type="warning" @click="addSkill">新增</el-button>
      </el-form-item>
    </el-form>
  </ResumeDialog>
  <!-- 项目经验 -->
  <ResumeDialog v-model="isShowProject" :modelTitle="'项目经验'">
    <el-form label-width="3rem" label-position="top">
      <section v-for="(domain, index) in projectList" :key="index">
        <el-form-item label="持续时间">
          <el-date-picker
            value-format="YYYY.MM.DD"
            v-model="domain.datetime"
            type="daterange"
            range-separator="到"
            start-placeholder="开始时间"
            end-placeholder="结束时间"
            size="default"
          />
        </el-form-item>
        <el-row>
          <el-form-item label="项目名称">
            <el-input type="text" v-model="domain.projectName"></el-input>
          </el-form-item>
          -
          <el-form-item label="职位">
            <el-input type="text" v-model="domain.post"></el-input>
          </el-form-item>
        </el-row>

        <el-form-item label="描述">
          <el-input type="textarea" v-model="domain.content"></el-input>
          <el-button
            style="margin-top: 0.2rem"
            type="danger"
            @click.prevent="deleteProject(domain)"
            >删除</el-button
          >
        </el-form-item>
      </section>
      <el-form-item>
        <el-button type="warning" @click="submitProject()">提交</el-button>
        <el-button type="warning" @click="addProject">新增</el-button>
      </el-form-item>
    </el-form>
  </ResumeDialog>
  <!-- 工作经验 -->
  <ResumeDialog v-model="isShowCompanyDialog" :modelTitle="'工作经验'">
    <el-form label-width="3rem" label-position="top">
      <section v-for="(domain, index) in workList" :key="index">
        <el-form-item label="持续时间">
          <el-date-picker
            value-format="YYYY.MM"
            v-model="domain.datetime"
            type="daterange"
            range-separator="到"
            start-placeholder="开始时间"
            end-placeholder="结束时间"
            size="default"
          />
        </el-form-item>
        <el-row>
          <el-form-item label="项目名称">
            <el-input type="text" v-model="domain.department"></el-input>
          </el-form-item>
          -
          <el-form-item label="职位">
            <el-input type="text" v-model="domain.post"></el-input>
          </el-form-item>
        </el-row>

        <el-form-item label="描述">
          <el-input type="textarea" v-model="domain.content"></el-input>
          <el-button
            style="margin-top: 0.2rem"
            type="danger"
            @click.prevent="deleteWork(domain)"
            >删除</el-button
          >
        </el-form-item>
      </section>
      <el-form-item>
        <el-button type="warning" @click="submitWork()">提交</el-button>
        <el-button type="warning" @click="addWork()">新增</el-button>
      </el-form-item>
    </el-form>
  </ResumeDialog>
</template>
<script>
import {
  computed,
  defineComponent,
  onMounted,
  reactive,
  toRefs,
  watch,
} from "vue";
import { useStore } from "vuex";
import ResumeDialog from "./ResumeDialog.vue";
import { ElMessage } from "element-plus";
export default defineComponent({
  components: {
    ResumeDialog,
  },
  setup() {
    const { getters, dispatch, commit } = useStore();
    const state = reactive({
      isShowBaseDialog: false,
      isShowContactDialog: false,
      isShowWorkDialog: false,
      isShowCertificateDialog: false,
      isShowSkillsList: false,
      isShowProject: false,
      isShowCompanyDialog: false,
      models: [
        {
          name: "基本信息",
          compKey: "contact",
          isFill: false,
          comment: "填写你的基本信息吧",
        },
        {
          name: "联系方式",
          compKey: "base",
          isFill: false,
          comment: "填写你的联系方式吧",
        },
        {
          name: "求职意向",
          compKey: "base",
          isFill: false,
          comment: "填写你的求职意向吧",
        },
        {
          name: "荣誉奖励",
          compKey: "work",
          isFill: false,
          comment: "填写你在学校获得的荣誉奖励吧",
        },
        {
          name: "技能证书",
          compKey: "schoolExperience",
          isFill: false,
          comment: "填写你的技能证书吧",
        },
        {
          name: "项目经历",
          isFill: false,
          comment: "填写你的项目经历吧",
        },
        {
          name: "工作经历",
          isFill: false,
          comment: "展示你的工作经历吧",
        },
      ],
      fillModels: [],
      nFillModels: [],
      baseValue: {},
      contactValue: {},
      workValue: {},
      certificateList: [{ value: "" }],
      skillList: [{ value: "" }],
      projectList: [{}],
      workList: [{}],
    });
    onMounted(() => {
      loadData();
    });
    // 监听baseValue是不是都填了
    watch(state.baseValue, (newVal, oldVal) => {
      const tmp = Object.assign({}, newVal);
      if (Object.keys(tmp).length == 7) {
        let onSchoolTime = {
          beginTime: newVal.onSchoolTime[0],
          endTime: newVal.onSchoolTime[1],
        };
        tmp.onSchoolTime = onSchoolTime;
        commit("resume/updateBase", tmp);
      }
    });
    // 监听contactValue是不是都填了
    watch(state.contactValue, (newVal, oldVal) => {
      if (Object.keys(newVal).length == 2) {
        commit("resume/updateContact", newVal);
      }
    });
    // 监听城市改变,传递新值
    watch(state.workValue, (newVal, oldVal) => {
      if (Object.keys(newVal).length == 2) {
        commit("resume/updateWork", newVal);
      }
    });
    watch(state.certificateList, (newVal, oldVal) => {
      commit("resume/updateCertificate", newVal);
    });
    watch(state.skillList, (newVal, oldVal) => {
      commit("resume/updateSkillList", newVal);
    });
    const loadData = () => {
      const resumeInfo = getters["resume/getResumeInfo"];
      state.models[0].isFill =
        resumeInfo.base.school != null &&
        resumeInfo.base.major != null &&
        resumeInfo.base.degree != null &&
        resumeInfo.base.hometown != null &&
        resumeInfo.base.onSchoolTime.beginTime != null &&
        resumeInfo.base.onSchoolTime.endTime != null;
      state.models[1].isFill =
        resumeInfo.contact.phone && resumeInfo.contact.email;
      state.models[2].isFill =
        resumeInfo.work.job != null && resumeInfo.work.city != null;
      state.models[3].isFill = resumeInfo.certificateList != null;
      state.models[4].isFill = resumeInfo.skillList != null;
      state.models[5].isFill = resumeInfo.projectExperience != null;
      state.models[6].isFill = resumeInfo.workExperience != null;
      for (const item of state.models) {
        if (item.isFill) {
          state.fillModels.push(item);
        } else {
          state.nFillModels.push(item);
        }
      }
    };
    const alreadyToFill = () => {
      return state.fillModels.length != 0;
    };
    const notToFill = () => {
      return state.nFillModels.length != 0;
    };
    const handleModelOpen = (item) => {
      if (item.name == "基本信息") {
        state.isShowBaseDialog = true;
      }
      if (item.name == "联系方式") {
        state.isShowContactDialog = true;
      }
      if (item.name == "求职意向") {
        state.isShowWorkDialog = true;
      }
      if (item.name == "荣誉奖励") {
        state.isShowCertificateDialog = true;
      }
      if (item.name == "技能证书") {
        state.isShowSkillsList = true;
      }
      if (item.name == "项目经历") {
        state.isShowProject = true;
      }
      if (item.name == "工作经历") {
        state.isShowCompanyDialog = true;
      }
    };
    const removeDomain = (domain) => {
      const index = state.certificateList.indexOf(domain);
      if (index !== -1) {
        state.certificateList.splice(index, 1);
      }
    };
    const addDomain = () => {
      state.certificateList.push({
        value: "",
      });
    };
    const removeSkill = (domain) => {
      const index = state.skillList.indexOf(domain);
      if (index !== -1) {
        state.skillList.splice(index, 1);
      }
    };
    const addSkill = () => {
      state.skillList.push({
        value: "",
      });
    };
    const addProject = () => {
      if (state.projectList.length >= 2) {
        ElMessage({
          message: "只能添加两个项目经历哦,请选择您最有代表性的两段项目经历",
          type: "success",
        });
        return;
      }
      state.projectList.push({
      });
    };
    const deleteProject = (domain) => {
      const index = state.projectList.indexOf(domain);
      if (index !== -1) {
        state.projectList.splice(index, 1);
      }
    };
    const deleteWork = (domain) => {
      const index = state.workList.indexOf(domain);
      if (index != -1) {
        state.projectList.splice(index, 1);
      }
    };
    const addWork = () => {
      if (state.workList.length >= 2) {
        ElMessage({
          message: "只能添加两个工作经历哦,请选择您最有代表性的两段经历吧",
          type: "success",
        });
        return;
      }
      state.workList.push({
      });
    };
    const submitProject=()=>{
      commit("resume/updateProjectList",state.projectList)
      state.isShowProject=false
    }
    const submitWork=()=>{
      commit("resume/updateWorkList",state.workList)
      state.isShowCompanyDialog=false
    }
    return {
      ...toRefs(state),
      alreadyToFill,
      handleModelOpen,
      addDomain,
      removeDomain,
      removeSkill,
      addSkill,
      submitProject,
      submitWork,
      notToFill,
      addProject,
      deleteProject,
      deleteWork,
      addWork,
    };
  },
});
</script>
<style lang="scss" scoped>
.sidebar-container {
  min-height: 30rem;
  background: #fff;
  position: absolute;
  right: 0.5rem;
  top: 50%;
  bottom: 50%;
  width: 15rem;
  border-radius: 0.2rem;
  color: var(--grg_main_theme);
  font-size: 0.7rem;
  box-sizing: border-box;
  padding: 0.3rem;
  transform: translate(0%, -50%);
  .sidebar-title {
    border-left: 0.1rem solid var(--grg_main_theme);
    padding-left: 0.3rem;
    font-weight: bold;
    margin-top: 0.3rem;
  }
  .sidebar-item {
    width: 100%;
    height: 3rem;
    border-radius: 0.2rem;
    margin-top: 0.3rem;
    background: #fcfbfc;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    box-sizing: border-box;
    padding: 0.5rem;
    .item-header {
      display: flex;
      flex-direction: row;
      align-items: center;
      color: var(--grg_main_theme);
      img {
        width: 1rem;
        height: 1rem;
      }
    }
    .item-body {
      color: var(--grg_main_theme);
      opacity: 0.5;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
    }
  }
}
</style>
